let firework;
(function() {
	if('ontouchstart' in window) {
		evnt_move = 'touchstart';
		evnt_end = 'touchend';
	} else {
		evnt_move = 'mousemove';
		evnt_end = 'mouseout';
	}

    const COLOR = ['#9ee', '#09f', '#f60', '#e99', '#e9e', '#ee9', '#99e', '#9e9'];
	let globalSize;
	let cvs = document.createElement('canvas');
	let ctx = cvs.getContext('2d');
	let ori = {};
    let center = 20;

	const TYPE_LENGTH = 3;

	function explotion(fw) {
		ctx.save();
		let radius = fw.flame_radius;
		switch(fw.type) {
			case 1:
				ctx.setLineDash([2, radius % ((10 + Math.random() * 70) + -1 * 20)]);
				ctx.translate(fw.x, fw.y);
				ctx.rotate(- radius / 10);
				ctx.beginPath();
					ctx.arc(0, 0, radius, 2 * Math.PI, 0, false);
					ctx.strokeStyle = COLOR[parseInt((radius - center) / 8) % 8];
					ctx.stroke();
				ctx.closePath();
				break;
			case 2:
				fw.flame_radius+=2;
				radius /= 2;
				let color = COLOR[Math.floor(Math.random() * 8)];
				ctx.translate(fw.x, fw.y);
				for(let i = 0; i < 30; i++) {
					ctx.rotate((1/15) * Math.PI);
					ctx.beginPath();
						ctx.moveTo(1, 1);
						ctx.lineTo(radius, radius);
						ctx.strokeStyle = (i % 2) ? fw.tail : color;
						ctx.stroke();
					ctx.closePath();
				}
				break;
			default:
				ctx.setLineDash([2, radius % (30 + Math.random() * 70)]);
				ctx.translate(fw.x, fw.y);
				ctx.rotate(radius / 10);
				ctx.beginPath();
					ctx.arc(0, 0, radius, 0, 2 * Math.PI, false);
					ctx.strokeStyle = COLOR[parseInt((radius - center) / 8) % 8];
					ctx.stroke();
				ctx.closePath();
		}
		ctx.restore();
	}

    class Firework {
        constructor(props) {
			this.x = globalSize * .5;
			this.y = globalSize;
            this.e_x = this.x;
			this.e_y = (.25 + Math.random() * .5) * globalSize;
			this.size = globalSize * (.1 + Math.random() * .4);

			this.tail = COLOR[Math.floor(Math.random() * 8)];
			this.type = Math.floor(Math.random() * TYPE_LENGTH);
            this.flame_radius = center;
        }
        update() {
			if(this.e_x == this.x && this.e_y == this.y) {
				this.flame_radius++;
				if(this.flame_radius < this.size) {
					explotion(this);
				} else {
					// 烟花消失，重新生成
					this.type = Math.floor(Math.random() * TYPE_LENGTH);
					this.flame_radius = center;
					this.size = (.1 + Math.random() * .4) * globalSize;

					this.tail = COLOR[Math.floor(Math.random() * 8)];
					this.e_x = (.25 + Math.random() * .5) * globalSize;
					this.e_y = (.25 + Math.random() * .5) * globalSize;
					this.x = globalSize * .5;
					this.y = globalSize;
				}
			} else {
				let dis = {
					x: this.e_x - this.x,
					y: this.e_y - this.y
				};
				ctx.save();
					ctx.beginPath();
						ctx.moveTo(this.x, this.y);
						ctx.lineTo(this.x + dis.x * .1, this.y + dis.y * .1);
					ctx.closePath();
					ctx.strokeStyle = this.tail;
					ctx.stroke();
				ctx.restore();
				
				if(Math.pow(Math.pow(dis.x, 2) + Math.pow(dis.y, 2), .5) <= 10) {
					this.x = this.e_x;
					this.y = this.e_y;
				} else {
					this.x = this.x + dis.x * .1;
					this.y = this.y + dis.y * .1;
				}
			}
        }
    }

	function box_init(box) {
		ori.x = box.offsetLeft;
		ori.y = box.offsetTop;
		globalSize = Math.min(box.offsetWidth, box.offsetHeight);

		cvs.style.width = globalSize;
		cvs.style.height = globalSize;
		cvs.width = globalSize;
		cvs.height = globalSize;

		box.innerHTML = '';
		box.appendChild(cvs);
	}

    let shot_fireworks = [];

	firework = function(id) {
		let box = document.getElementById(id);
		box_init(box);

		shot_fireworks.push(new Firework());
		setTimeout(function() {
			shot_fireworks.push(new Firework());
		}, globalSize / 2 * 10);
		setTimeout(function() {
			shot_fireworks.push(new Firework());
		}, globalSize / 2 * 20);

		update();
	}

	function update() {
		ctx.fillStyle = 'rgba(48, 48, 48, .2)';
		ctx.fillRect(0, 0, globalSize, globalSize);

		shot_fireworks.map(function(firework) {
			firework.update();
		});

		window.requestAnimationFrame(update);
	}
}());

firework('firework');
